@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
/* TODO Add site wide styles */


body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    font-family: 'Open Sans', sans-serif;
}



button {
    background: none;
    border: none;
    color: #FFF201;
    text-decoration: none;
    padding: 5px;
    font-size: 1.2em;
}
/* Colors:
    mid-blue: #21B5DA
    light-blue: #83D5DD
    dark-blue: #015498
    yellow: #FFF201
*/

.headerStrip {
    display: flex;
    justify-content: center;
    background-color: #015498;
    height: 100px;

}

.logo {
    height: 100px;
}


.form-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #abdee2;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    top: 100px;
}

.login-header {
    color: #FFF201;
    font-weight: 600;
}

.signup-header,
.signup-link {
    padding: 3px;
    text-decoration: none;
    color: #015498;
}

.signup-link:hover {
    color: #FFF201;
}


.signupContainer {
    display: flex;
    align-items: center;
}

.signupForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    color: #015498;

}

.lastName,
.confirmPass {
    margin-left: 4px;
}

.name-fields {
    display: flex;
}

.signup-label {
    margin-bottom: 5px;
}

.signup-input {
    opacity: 60%;
    margin-left: 4px;
    border-radius: 4px;
    border: none;
}

.signupImage {
    height: 200px;
    border-radius: 60%;
}

.buttons {
    display: flex;
    justify-content: space-evenly;
    width: 50%;
}

.signup-button,
.demo-button {
    
    margin: 3px;
    background-color: none;
    color: #015498;
}

.signup-button:hover,
.demo-button:hover {
    color: #FFF201;

} 
.navbar {
    display: flex;
    align-items: flex-end;
    background-color: #015498;
    height: 100px;
    padding: 5px;
    justify-content: space-between;
}

.home {
    color: #FFF201;
    text-decoration: none;

}

.login-buttons {
   padding: 25px;
}

.signup {
    margin-left: 15px;
    border: 1px solid #FFF201;
    border-radius: 5px;
}

.signup:hover {
    color: black;
    background-color: #FFF201;
}

.login:hover {
    color: #fffcc5;

}

.login,
.signup {
    color: #FFF201;
    text-decoration: none;
    padding: 5px;
    font-size: 1.2em;
}



.logo {
    height: 100px;
}

/* ---LOGGED IN------ */

.account-menu {
    padding-right: 10px;
}

.account-icon {
    font-size: 3.5em;
    color: #FFF201;
    padding: 20px;
}

.profile-dropdown {
    color:#FFF201;
    list-style:  none;
}

.logOut {
    color:#FFF201;
    padding-top: 10px;
    font-size: 18px;

}

#modal {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#modal-background {

  position: fixed;
  display:flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

#modal-content {
  position: absolute;
  background-color:white;
  
}
